import { Component, h, Host, Prop } from '@stencil/core';

@Component({
  tag: 'ks-order-progress',
  styleUrl: 'orderProgress.css'
})
export class OrderProgress {

  @Prop() current: number;
  @Prop() cart: string = 'Koszyk';
  @Prop() shipping: string = 'Dane do wys≈Çki';
  @Prop() confirmation: string = 'Potwierdzenie';

  render() {
    let heading = this.cart;
    if(this.current == 1) heading = this.shipping;
    else if(this.current == 2) heading = this.confirmation;

    return <Host><h1>{heading}</h1></Host>
  }
}